body {
    background-image: url("../assets/photo-of-buildings-during-nighttime-2603464.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
}

.CounterCard {
    width: 18rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.9);
    color: #f1f1f1;
    padding: 20px;
    box-shadow: 0 8px 16px 0 rgba(197, 126, 126, 0.9);
    transition: 0.3s;
    border-radius: 10px;
}

.CounterCard:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}